.button {
  display: block;
  font-size: 16px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  box-sizing: border-box;
  cursor: pointer;

  &:hover {
    background-color: $red;
    border: 1px solid $red;
  }
}

.button-big {
  padding-left: 65px;
  padding-right: 65px;
  padding-top: 15px;
  box-sizing: border-box;
  height: 58px;
  border: 1px solid $blue;
  margin-right: 15px;
}

.button-project {
  padding-top: 0;
}

.button-blue {
  background-color: $blue;
}

.button-white {
  border: 1px solid #ffffff;
}

.button-middle {
  padding-top: 7px;
  width: 182px;
  height: 45px;
}

.button-middle +.button-white {
  margin-left: 4px;
  color: $blue;
  border: 2px solid $blue;

  &:hover {
    color: #ffffff;
    border: none;
  }
}

.buttons-wrapper__main {
  margin-top: 18px;
}

.button-cross {
  position: absolute;
  top: 30px;
    left: 720px;
  background-image: url("../img/cross.svg");
  background-color: #fffffff;
  width: 24px;
  height: 24px;
}
